import {getUserInfo} from '../../service/userManger'
export default {
    namespace:'main',
    state:{
        openKeys:[],
        userInfo:{}
    },
    effects:{
        *getUserInfo(payload,{call,put}){
            let userInfo = yield call(getUserInfo);
            if(userInfo.code === 1){
                yield put({type:'setUserInfo',...userInfo.data});
                yield put({type:'global/getViewAuthority',user_id:userInfo.data.user_id});
            }
        }
    },
    reducers:{
        setOpenKeys(state,{openKeys}){
            state.openKeys = [...openKeys];
            return {...state}
        },
        setUserInfo(state,payload){
            state.userInfo = payload;
            return {...state}
        }
    }
}